import styled from 'styled-components';

export const Container = styled.div``;

export const Overlay = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    margin: 0 20px;
`;

export const Inner = styled.div`
    position: relative;
    width: 100%;
    max-width: 900px;
    margin: auto;
    
    video {
        height: 100%;
        width: 100%;
    }
`;

export const Close = styled.button`
position: absolute;
right: 15px;
top: 15px;
width: 22px;
height: 22px;
opacity: 1;
background-color: transparent;
border-color: white;
border-style: solid;
border-radius: 50%;
cursor: pointer;
transform: rotate(45deg);

&:before,
&:after {
    position: absolute;
    left: 8px;
    top: 1px;
    content: ' ';
    height: 15px;
    width: 2px;
    background-color: white;
}

&:hover {
    transform: rotate(90deg);
}

&:after {
    transform: rotate(-90deg);
}

`;

export const Button = styled.button`
    background-color: #e50914;
    border-color: #ff0a16;
    width: 115px;
    height: 45px;
    text-transform: uppercase;
    font-weight: bold;
    color: white;
    font-size: 18px;
    height: 45px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 0;
    
    &:hover {
        transform: scale(1.05);
        background-color: #ff0a16;
    }
`;